<template>
    <div v-loading="loading" :element-loading-text="loadingText">
        <el-form :label-width="labelWidth" :model="userModel" :ref="reFrom" :rules="rules" label-position="left">
            <el-form-item label="" prop="username">
                <el-input v-model.trim="userModel.username" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="avatar_url" id="avatar" style="display: flex;align-items: center;position: relative;">
                <Upload :avatar_url="userModel.avatar_url" :username="userModel.username" @uploadSuccess="uploadSuccess"></Upload>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input :type="!showPassword ? 'password' : 'text'" placeholder="" v-model="userModel.password" :readonly="!showPassword">
                    <el-button slot="append" :icon="showPassword ? 'el-icon-open' : 'el-icon-turn-off'" @click="showPassword = !showPassword">
                        {{showPassword ? ' ' : ' '}}
                    </el-button>
                </el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model.trim="userModel.email" type="email" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone_number">
                <el-input v-model.number="userModel.phone_number" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="status">
                <Status :status="userModel.status" @changeStatus="changeStatus"></Status>
            </el-form-item>
            <el-form-item style="text-align: center">
                <Submit :reFrom="reFrom" :model="userModel" :url="url" :refs="refs"></Submit>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import apiLists from '../../api/api'
    import $url from '../../api/url'
    import func from '../../api/func'
    import Submit from '../common/Submit'
    import Upload from '../common/Upload'
    import {mapGetters} from 'vuex'
    import Status from '../common/Status'
    export default {
        name: 'lists',
        components: {Status, Submit, Upload},
        data () {
            return {
                labelWidth: '100px',
                loading: false,
                loadingText: '',
                url: $url.userUpdate,
                refs: this.$refs,
                reFrom: 'created',
                showPassword: false,
                userModel: {
                    username: '',
                    email: '',
                    password: '',
                    salt: func.setTime(),
                    status: 1,
                    role_id: 2,
                    phone_number: '',
                    created_at: func.getTimestamp(),
                    updated_at: func.getTimestamp(),
                    avatar_url: ''
                },
                rules: {
                    username: [{required: true, message: '', trigger: 'blur'}],
                    password: [{required: true, message: '', trigger: 'blur'}],
                    email: [{required: true, message: '', trigger: 'blur'}, {type: 'email', message: '', trigger: 'blur'}],
                    status: [{required: true, message: '', trigger: 'change'}],
                    avatar_url: [{required: true, message: '', trigger: 'change'}],
                    phone_number: [{required: true, message: '', trigger: 'change'}]
                }
            }
        },
        computed: {
            ...mapGetters(['userInfo'])
        },
        methods: {
            /**
             * todo:
             * @param status
             */
            changeStatus: function (status) {
                this.userModel.status = status
            },
            /**
             * TODO:
             */
            userBind: function () {
                apiLists.UserBind({'remember_token': this.userInfo.token}).then(response => {
                    if (response && response.data.code === 200) {
                        this.userModel = response.data.item
                    }
                })
            },
            /**
             * TODO:
             * @param src
             */
            uploadSuccess: function (src) {
                this.userModel.avatar_url = src
            }
        },
        mounted () {
            this.$nextTick(function () {
                this.userBind()
            })
        },
        created () {
            this.userModel.username = this.userInfo.username
            this.userModel.avatar_url = this.userInfo.avatar_url
            this.userModel.remember_token = this.userInfo.token
        }
    }
</script>

<style scoped>

</style>



