import func from './func'
import $url from './url'
// 
const uploadConfig = {
    action: process.env.API_ROOT + $url.fileUpload.replace('/', ''), //  
    methods: 'POST', //  
    token: localStorage.token, //  tokentokensessionStorage
    name: 'file', //  
    size: 2, //    Kb, 1M = 1024Kb
    rand: true, // 
    accept: 'image/png, image/gif, image/jpeg, image/bmp, image/x-icon, image/jpg' //  
}

// toolbar
const toolOptions = [
    ['bold', 'italic', 'underline', 'strike'],
    ['blockquote', 'code-block'],
    [{'header': 1}, {'header': 2}],
    [{'list': 'ordered'}, {'list': 'bullet'}],
    [{'script': 'sub'}, {'script': 'super'}],
    [{'indent': '-1'}, {'indent': '+1'}],
    [{'direction': 'rtl'}],
    [{'size': ['small', false, 'large', 'huge']}],
    [{'header': [1, 2, 3, 4, 5, 6, false]}],
    [{'color': []}, {'background': []}],
    [{'font': []}],
    [{'align': []}],
    ['clean'],
    ['link', 'image', 'video']
]

// handler
const handlers = {
    image: function image () {
        let self = this
        let fileInput = this.container.querySelector('input.ql-image[type=file]')
        if (fileInput === null) {
            fileInput = document.createElement('input')
            fileInput.setAttribute('type', 'file')
            // 
            if (uploadConfig.name) {
                fileInput.setAttribute('name', uploadConfig.name)
            }
            // 
            fileInput.setAttribute('accept', uploadConfig.accept)
            fileInput.classList.add('ql-image')
            // 
            fileInput.addEventListener('change', function () {
                // 
                if (uploadConfig.size && fileInput.files[0].size >= uploadConfig.size * 1024) {
                    fileInput.value = ''
                    return
                }
                // formData
                let formData = new FormData()
                formData.append(uploadConfig.name, fileInput.files[0])
                // tokentoken
                if (uploadConfig.token) {
                    formData.append('token', uploadConfig.token)
                }
                // 
                if (uploadConfig.rand) {
                    formData.append('rand', uploadConfig.rand)
                }
                // 
                let xhr = new XMLHttpRequest()
                xhr.open(uploadConfig.methods, uploadConfig.action, true)
                xhr.setRequestHeader('Authorization', `${func.setPassword(func.setRandom(32), func.setRandom(12))}${localStorage.token}${func.setPassword(func.setRandom(32), func.setRandom(12))}`)
                // 
                xhr.upload.onloadstart = function (e) {
                    fileInput.value = ''
                    // console.log('')
                }
                // 
                xhr.onload = function (e) {
                    if (xhr.status === 200) {
                        let res = JSON.parse(xhr.responseText)
                        let length = self.quill.getSelection(true).index
                        self.quill.insertEmbed(length, 'image', res.item.src)
                        self.quill.setSelection(length + 1)
                    }
                    fileInput.value = ''
                }
                // 
                xhr.upload.onerror = function (e) {
                }
                // 
                xhr.upload.onloadend = function (e) {
                    // console.log('')
                }
                xhr.send(formData)
            })
            this.container.appendChild(fileInput)
        }
        fileInput.click()
    }
}
// 
export const editorOption = {
    placeholder: 'Compose an epic...',
    theme: 'snow', // 
    modules: {
        toolbar: {
            container: toolOptions, // 
            handlers: handlers // 
        }
    }
}



